import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Helmet } from "react-helmet";

import HomePage from "./pages/HomePage";
import ChatPage from "./pages/ChatPage";

function App() {
  return (
    <Router>
      <Helmet>
        <title>theRoom by Hubert Kozik</title>
        <meta author="Hubert Kozik" />
      </Helmet>
      <Switch>
        <Route path="/theRoom" exact component={HomePage} />
        <Route path="/theRoom/chat" component={ChatPage} />
      </Switch>
    </Router>
  );
}

export default App;

