import React, { useState, useEffect } from "react";
import axios from "axios";
import Pusher from "pusher-js";
import { use100vh } from "react-div-100vh";

import "../styles/Chat.scss";
import { Avatar } from "@material-ui/core";
import Message from "./Message";
import SendIcon from "@material-ui/icons/Send";

function Chat(props) {
  const [messages, setMessages] = useState([]);
  const [inputMessage, setInputMessage] = useState("");
  const height = use100vh() - 160 - 10;
  const { username } = props;

  useEffect(() => {
    axios.get("https://theroom-mern.herokuapp.com/message/sync").then((res) => {
      setMessages(res.data);
    });
  }, []);

  useEffect(() => {
    const pusher = new Pusher("c16aa5f342aa86d3aba4", {
      cluster: "eu",
    });

    const channel = pusher.subscribe("messages");
    channel.bind("insertMessage", function (data) {
      setMessages([...messages, data]);
    });

    document
      .getElementsByClassName("chat__messages")[0]
      .scrollTo(
        0,
        document.getElementsByClassName("chat__messages")[0].scrollHeight
      );

    return () => {
      channel.unbind_all();
      channel.unsubscribe();
    };
  }, [messages]);

  const handleSubmit = (e) => {
    e.preventDefault();
    const time = new Date().toISOString(); //.slice(0, 10);
    axios.post("https://theroom-mern.herokuapp.com/message/new", {
      username: username,
      timestamp: time,
      message: inputMessage,
    });
    setInputMessage("");
  };

  const handleInput = (e) => {
    setInputMessage(e.target.value);
  };

  return (
    <main className="chat">
      <div className="chat__header">
        <div className="chat__roomInfo">
          <Avatar />
          <h4 className="chat__roomName">Room 1</h4>
        </div>
      </div>

      <div className="chat__messages" style={{ height: height }}>
        {messages.map((message) => (
          <Message
            message={message.message}
            timestamp={message.timestamp}
            username={message.username}
            key={message._id}
          />
        ))}
      </div>
      <div className="chat__footer">
        <form className="chat__form" onSubmit={handleSubmit}>
          <input
            type="text"
            placeholder="Write your message..."
            className="chat__input"
            value={inputMessage}
            onChange={handleInput}
          />
          <button type="submit" className="chat__submit">
            <SendIcon />
          </button>
        </form>
      </div>
    </main>
  );
}

export default Chat;
