import React, { useEffect, useRef } from "react";
import { use100vh } from "react-div-100vh";
import gsap from "gsap";

import "../styles/Login.scss";

import { ReactComponent as LoginSvg } from "../svgs/login.svg";
import BrandImage from "./BrandImage";

function Login(props) {
  const svgWrapper = useRef(null);
  const loginWrapper = useRef(null);
  const { handleSubmit, setUsername, username } = props;

  useEffect(() => {
    const [elements] = svgWrapper.current.children;
    const login = loginWrapper.current.children;

    const background = elements.getElementById("background");
    const door_and_floor = elements.getElementById("door_and_floor");
    const human = elements.getElementById("human");

    gsap.set([...background.children, door_and_floor, human, login], {
      autoAlpha: 0,
    });

    const tl = gsap.timeline({ defaults: { ease: "power3.inOut" } });

    tl.fromTo(
      door_and_floor,
      { y: "-=300" },
      { duration: 0.5, y: "+=300", autoAlpha: 1 }
    )
      .fromTo(human, { x: "+=300" }, { duration: 1, x: "-=300", autoAlpha: 1 })
      .to(background.children, { duration: 0.5, autoAlpha: 1, stagger: 0.3 });

    const tl2 = gsap.timeline({ defaults: { ease: "power3.inOut" } });

    tl2.fromTo(
      login,
      { y: "+=300" },
      { duration: 1, y: "-=300", autoAlpha: 1 }
    );
  }, []);

  return (
    <div className="login" style={{ height: use100vh() }}>
      <BrandImage />
      <div ref={svgWrapper}>
        <LoginSvg className="login__svg" />
      </div>
      <form onSubmit={handleSubmit} className="login__form" ref={loginWrapper}>
        <input
          type="text"
          placeholder="Type your username..."
          className="login__input"
          onChange={setUsername}
          value={username}
        />
        <button type="submit" className="login__submit">
          Send
        </button>
      </form>
    </div>
  );
}

export default Login;
