import React, { useState } from "react";
import { useHistory } from "react-router-dom";

import "../styles/Navbar.scss";
import { Avatar, IconButton } from "@material-ui/core";
import AddBoxIcon from "@material-ui/icons/AddBox";
import IndeterminateCheckBoxIcon from "@material-ui/icons/IndeterminateCheckBox";

import BrandImage from "./BrandImage";

function Navbar(props) {
  const [userPanelClick, setUserPanelClick] = useState(false);
  const { username } = props;
  let history = useHistory();

  const handleUserPanelClick = () => {
    setUserPanelClick(!userPanelClick);
  };

  const handleLogOut = () => {
    localStorage.removeItem("username");
    history.push("/chat");
  };

  return (
    <nav className="nav">
      <BrandImage />
      {username && (
        <>
          <div className="nav__userInfo">
            {userPanelClick ? (
              <button className="nav__logout" onClick={handleLogOut}>
                Wyloguj
              </button>
            ) : (
              <>
                <Avatar />
                <h4 className="nav__userName">{username}</h4>
              </>
            )}
            <IconButton onClick={handleUserPanelClick}>
              {userPanelClick ? <IndeterminateCheckBoxIcon /> : <AddBoxIcon />}
            </IconButton>
          </div>
        </>
      )}
    </nav>
  );
}

export default Navbar;
